.App {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  padding-top: 70px;
  padding-bottom: 100px;
}
.App .navbar-inverse.navbar-fixed-top {
  background-color: #3f5e78;
  border: none;
}
.App .navbar-inverse.navbar-fixed-top .navbar-header a,
.App .navbar-inverse.navbar-fixed-top .navbar-nav > li > a,
.App .navbar-inverse.navbar-fixed-top .dropdown-toggle {
  color: white;
}
.App .navbar-inverse.navbar-fixed-top .navbar-header a:hover,
.App .navbar-inverse.navbar-fixed-top .navbar-nav > li > a:hover,
.App .navbar-inverse.navbar-fixed-top .dropdown-toggle:hover {
  background-color: #25455e;
}
.App .btn-file {
  position: relative;
  overflow: hidden;
}
.App .btn-file input[type=file] {
  position: absolute;
  top: 0;
  right: 0;
  min-width: 100%;
  min-height: 100%;
  font-size: 100px;
  text-align: right;
  filter: alpha(opacity=0);
  opacity: 0;
  outline: none;
  cursor: inherit;
  display: block;
}
.App .projects-container ul.nav-stacked {
  vertical-align: middle;
  width: 100%;
  padding: 30px 0;
  border-right: 1px solid #ddd;
}
.App .projects-container ul.nav-stacked a {
  -webkit-transition: all 0.08s linear;
  -o-transition: all 0.08s linear;
  transition: all 0.08s linear;
  color: inherit;
}
.App .projects-container ul.nav-stacked > li.active {
  background-color: #a4ceef;
  color: white;
}
.App .projects-container ul.nav-stacked > li:hover {
  background-color: #d07c20;
}
.App .progress {
  height: 100%;
  cursor: progress;
}
.App .progress .progress-bar {
  vertical-align: middle;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.3333333;
  border-radius: 0px;
}



